$blue_01: #1F3F68;
$blue_04: #5A98D0;
html {
  font-family: GilroyLight;
}

.main-frame {
  background: linear-gradient(rgba(152, 195, 232, 0), #98C3E840);
  height: 100%;
  max-width: 100%;
  &:before {
    content: '';
    position: absolute;
    height: 817.77px;
    width: 966.19px;
    right: 50px;
    top: 150px;
    background-image: url("../images/main-frame__bg-image.png");
    background-repeat: no-repeat;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 50px 80px 30px 80px;
}

h1 {
  color: $blue_01;
  font-family: GalienRegular;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.main-frame__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-top: 10%;
  h1 {
    font-weight: 600;
    font-size: 74px;
    line-height: 84px;
    letter-spacing: 0.06em;
  }
  p {
    color: $blue_01;
    font-size: 18px;
    line-height: 28px;
    margin: 4% 0 6% 0;
    letter-spacing: 0.05em;
  }
  button {
    font-size: 20px;
    line-height: 26px;
    letter-spacing: 0.04em;
    padding: 20px 65px;
    background-color: $blue_04;
    border-radius: 10px;
    color: #fff;
    &:hover {
      transform: scale(1.05);
      transition: .5s
    }
  }
}